<template>
  <div id="Nav">
    <div class="left">
      <v-card class="menu" elevation="2" >
        <v-row align="center" class="spacer spacing-playground mt-5" no-gutters @click="dialog = true">
          <v-col cols="4" sm="4"  md="4" >
            <v-avatar  elevation="2" color="primary" size="30" >
              <v-icon dark>
               mdi-account
              </v-icon>
            </v-avatar>
          </v-col>
          <v-col cols="4" sm="6" md="6">
            {{Name}}
          </v-col>
        </v-row>

        <v-row align="center" class="spacer spacing-playground mt-5" no-gutters v-for="(v,k) in menuList" :key="k" @click="clickItem(v)">
          <v-col cols="4" sm="4"  md="4" >
            <v-avatar  elevation="2" color="primary" size="30" >
              <v-icon dark>
                {{v.icon}}
              </v-icon>
            </v-avatar>
          </v-col>
          <v-col cols="4" sm="6" md="6">
            {{v.name}}
          </v-col>
        </v-row>
      </v-card>
      <div>
        <v-switch
            v-model="dark"
            label="Dark"
            hide-details
            @change="change"
        ></v-switch>
      </div>
    </div>
    <div class="right">
      <div class="content">
        <router-view/>
      </div>
    </div>
    <v-dialog  persistent v-model="dialog" width="400" >
      <v-card class="pa-7">
      <v-text-field v-model="Name" label="昵称" required :rules="rules"></v-text-field>
      <v-btn depressed elevation="2" @click="save">
        确定
      </v-btn>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
name: "Nav",
  data(){
    return {
      dark:false,
      Name:"",
      dialog:false,
      rules: [
        value => !!value || '请输入昵称！'
      ],
      menuList:[
        {name:"计时器",icon:"mdi-alarm",path:"/index"},
        {name:"统计",icon:"mdi-alarm",path:"/index"}
      ]
    }
  },
  components:{},
  computed:{},
  methods:{
    save(){
      if(this.Name.length<1){
        return
      }
      localStorage.setItem('Name',this.Name)
      this.dialog = false
    },
    clickItem(v){
      this.$router.push({path:v.path})
    },
    change(){
      this.$vuetify.theme.dark = this.dark
      localStorage.setItem('dark',this.dark)
    },
    init(){
      // 初始主题
      if(localStorage.getItem('dark')){
        this.$vuetify.theme.dark = localStorage.getItem('dark') === 'true'
        this.dark = this.$vuetify.theme.dark
      }
      // 获取名字
      if(localStorage.getItem('Name')){
        this.Name = localStorage.getItem('Name')
        return
      }
      this.dialog = true
    }
  },
  created() {
  },
  mounted() {
    this.init()
  }
}
</script>

<style scoped lang="less">
#Nav{
  width: 100vw;
  height: 100vh;
  padding-top: 10vh;
}
.left{
  height: 80vh;
  width: 10vw;
  display: inline-block;
  margin-right: 3vw;
  margin-left: 3vw;
  vertical-align: top;
}
.right{
  display: inline-block;
  height: 80vh;
  width: 81vw;
  margin-right: 3vw;
  vertical-align: top;
}
.menu{
  width: 100%;
  height: 100%;
  border-radius: 10px!important;
  padding-left: 1vw;
  padding-top: 1vw;
  padding-bottom: 1vw;
  &:hover{
    box-shadow: 0 1px 1px -2px rgb(0 0 0 / 10%), 0px 1px 1px 0px rgb(0 0 0 / 7%), 0px 1px 1px 0px rgb(0 0 0 / 6%) !important;
  }
}
.content{
  width: 100%;
  height: 100%;
}
.spacer{
  cursor: pointer;
}
</style>
