<template>
  <div id="home">
    <div class="left">
      <div class="top">
        <v-card class="menu" elevation="2" >
          <div class="input">
            <div class="box">
              <v-slider
                  v-model="timeNum"
                  label="计时时长(小时)"
                  :max="24"
                  :min="1"
                  thumb-label="always"
              ></v-slider>
              <v-btn class="btn" depressed elevation="2" color="primary" @click="save">
                开始
              </v-btn>
              <v-btn class="btn" depressed elevation="2" color="#993232" @click="cancel">
                重置
              </v-btn>
              <v-btn class="btn" depressed elevation="2" color="secondary" @click="stop">
                {{isStop?'恢复':'暂停'}}
              </v-btn>
            </div>
          </div>
        </v-card>
      </div>
      <div class="bottom">
        <v-card class="menu" elevation="2" >

        </v-card>
      </div>
    </div>
    <div class="right">
      <v-card class="menu" elevation="2" >
        <div class="content" :style="{color:timeOut?'#993232':''}">
         <span class="timeout" v-if="timeOut">超时</span> {{ date }}
        </div>
      </v-card>
    </div>
    <v-dialog  persistent v-model="dialog" width="250" >
      <v-card class="pa-7" style="text-align: center">
        <p style="margin-bottom: 20px">是否继续上次计时</p>
        <v-btn class="btn" color="primary" depressed elevation="2" @click="ok(true)">
          确定
        </v-btn>
        <v-btn class="btn" color="blue-grey" depressed elevation="2" @click="ok(false)">
          取消
        </v-btn>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
name: "home",
  data(){
    return {
      dialog:false,
      timeNum:4,
      date:"00:00:00",
      timerBox:null,
      timeOut:false,
      isStop:false,
      isLastTime:false,
      lastTime:{
        zTime:0,
        nTime:0,
        time:0
      }
    }
  },
  components:{},
  computed:{},
  methods:{
    ok(v){
      if(v){
        this.isLastTime = v
        this.lastTime = JSON.parse(localStorage.getItem('lastTime'))
        this.timer()
      }
      this.dialog = false
    },
    stop(){
      this.isStop =!this.isStop
    },
    cancel(){
      this.date = "00:00:00"
      this.timeOut = false
      clearInterval(this.timerBox)
      // this.save()
    },
    save(){
      this.date = "00:00:00"
      this.timeOut = false
      clearInterval(this.timerBox)
      this.timer(this.timeNum)
    },
    timer(num){
      let zTime = 0
      let nTime = 0
      if(this.isLastTime){
        zTime = this.lastTime.zTime
        nTime = this.lastTime.nTime
      }else {
         zTime = new Date().getTime()+3600000*num
         nTime = new Date().getTime()
      }
      this.timerBox =  setInterval(()=>{
        if(!this.isStop){
          zTime -= 1000
          this.lastTime = {
            time:zTime - nTime,
            zTime,
            nTime
          }
          localStorage.setItem("lastTime",JSON.stringify(this.lastTime))
          // let d = this.fNum(Math.floor(time/(1000*60*60*24)))  //计算天数
          let h = Math.floor(this.lastTime.time/(1000*60*60)%24)  //计算小时数
          let m = Math.floor(this.lastTime.time/(1000*60)%60)//计算分钟数
          let s = Math.floor(this.lastTime.time/1000%60)  //计算秒数
          this.date = this.fDate(h,m,s)
        }
      },1000)
    },
    fNum(num){
      // 补位
      if(num*1<=0){
       return  "00"
      }
      if(num*1<10){
        return  "0"+num
      }
      return num
    },
    fDate(h,m,s){
      // 判断是否超时
      let _this = this
      function abs(num){
        if(num.toString().indexOf('-')>-1){
          _this.timeOut = true
          return  num.toString().split('-')[1]*1-1
        }else {
          _this.timeOut = false
          return num
        }
      }
      return `${this.fNum(abs(h))}:${this.fNum(abs(m))}:${this.fNum(Math.abs(s))}`
    }
  },
  created() {
  },
  mounted() {
    if(localStorage.getItem('lastTime')){
      this.dialog = true
    }
  }
}
</script>

<style scoped lang="less">
#home{
  width: 100%;
  height: 100%;
}
.top{
  height: 48%;
  width: 100%;
  margin-bottom: 4%;
  vertical-align: top;
}
.bottom{
  height: 48%;
  width: 100%;
  vertical-align: top;
}
.left{
  vertical-align: top;
  border-radius: 10px!important;
  height: 100%;
  width: 49%;
  display: inline-block;
}
.right{
  border-radius: 10px!important;
  height: 100%;
  width: 49%;
  margin-left: 2%;
  display: inline-block;
}
.menu{
  width: 100%;
  height: 100%;
  border-radius: 10px!important;
  padding-left: 1vw;
  padding-top: 1vw;
  padding-bottom: 1vw;
  &:hover{
    box-shadow: 0 1px 1px -2px rgb(0 0 0 / 10%), 0px 1px 1px 0px rgb(0 0 0 / 7%), 0px 1px 1px 0px rgb(0 0 0 / 6%) !important;
  }
}
.content{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  font-size: 8rem;
  font-weight: 600;
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
}
.timeout{
  color: #993232;
  font-size: 4rem;
  margin-right: 1rem;
  width: 100%;
  text-align: center;
}
.input{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  .box{
    width: 50%;
    text-align: center;
  }
}
.btn{
  margin: 10px;
  color: white;
}
</style>
